.pagination {
  display: flex;
  gap: 12px;
}
.button {
  width: 26px;
  height: 24px;

  background: #f5f5f5;
  border: 1px solid #eeeeee;
  border-radius: 4px;

  font-weight: 500;
  font-size: 12px;
  line-height: 100%;
  letter-spacing: -0.01em;

  color: #404b52;
}
.buttonActive {
  padding: 6px 10px;

  background: #5932ea;
  border: 1px solid #5932ea;
  border-radius: 4px;
  color: var(--color-icon-active);
}
